// -----------------------------------------------------------------------------
// PROJECT MODAL LAYOUT
// -----------------------------------------------------------------------------
@use "../abstracts/variables" as *;
@use "../abstracts/mixins" as *;
@use "../abstracts/mixins-effects" as *;

.project-wrapper {
  .additional-project-img {
    @include flex-column;
    flex-wrap: wrap;
    @include gap-md;
    margin: 24px 0 0 0;
    width: 100%;
    justify-content: flex-start;

    img {
      width: 180px;
      height: auto;
      @include tile-style;
      object-fit: cover;
      background: $light;
      margin: 0;
      display: block;
      transition: transform 0.2s;
    }
    img:hover {
      transform: scale(1.04) translateY(-2px);
      box-shadow: 0 6px 18px $brand-primary-100;
    }
  }

  @media (max-width: 900px) {
    .additional-project-img {
      gap: 12px;
      img {
        width: 100px;
      }
    }
  }
  max-width: 1140px;
  margin: 0 auto;
  @include flex-column;
  @include gap-md;

  .project-row {
    display: flex;
    @include gap-md;
    width: 100%;
    flex-wrap: wrap;
  }

  .main-project-img {
    flex: 1 1 50%;
    width: 50%;
    max-width: 50%;
    @include flex-center;


    img {
      width: 100%;
      height: auto;
      display: block;
      border-radius: $radius-lg;
    }
  }

  .project-description,
  .project-deliverables {
    flex: 1 1 47%;
    width: 50%;
    max-width: 50%;
    @include flex-column;
    justify-content: center;
    padding: 24px 0 24px 32px;

  }
  .project-description {
    padding: 24px 0 24px 32px;
  }
  .project-deliverables {
    padding: 24px 32px 24px 0;
  }
  .secondary-project-img {
    flex: 1 1 50%;
    width: 50%;
    max-width: 50%;
    @include flex-center;

  }

  // Responsive adjustments
  @media (max-width: 900px) {
    .project-row {
      flex-direction: column;
      @include gap-md;
    }
    .main-project-img,
    .project-description,
    .project-deliverables,
    .secondary-project-img {
      width: 100%;
      max-width: 100%;
      min-width: 0;
      padding-left: 0;
      padding-right: 0;
    }
  }
}
// -----------------------------------------------------------------------------
// TECHNOLOGY TAGS
// -----------------------------------------------------------------------------

// =============================
// Technologies Used Section (inside .project-wrapper)
// =============================
.project-wrapper {
  // ...existing code...
  .tech-used {
    margin-top: 24px;
    padding-top: 20px;
    border-top: 1px solid $color-border-light;

    strong {
      display: block;
      margin-bottom: 12px;
      color: $primary-dark;
      font-size: 1.1rem;
      font-weight: 600;
    }

    .project-tech-list {
      list-style: none;
      padding: 0;
      margin: 0;
      display: flex;
      flex-wrap: wrap;
      gap: 8px;

      li {
        display: flex;
        align-items: center;
        gap: 6px;
        background: linear-gradient(135deg, $brand-primary-100, $brand-primary-50);
        color: $primary-dark;
        padding: 8px 12px;
        border-radius: 20px;
        font-size: 0.875rem;
        font-weight: 500;
        border: 1px solid $brand-primary-200;
        @include transition-fast(all, 0.2s);

        &:hover {
          background: linear-gradient(135deg, $brand-primary-100, $brand-primary-100);
          transform: translateY(-1px);
          box-shadow: 0 2px 8px $brand-primary-200;
        }

        i {
          font-size: 1rem;
          // Technology brand colors
          @include brand-icon-colors-direct;
        }
      }
    }
  }
}
